import React, { useState, useEffect, useRef } from "react";
import { gsap } from "gsap";
import { LuChevronDown, LuChevronUp } from "react-icons/lu";
import experienceData from '../data/experienceData';

const About = () => {
  const [showAboutPlus, setShowAboutPlus] = useState(false);
  const aboutTxtRef = useRef(null);
  const aboutPlusRef = useRef(null);

  const clickMoreView = () => {
    setShowAboutPlus((prev) => !prev);
  };

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 100) {
        gsap.to(aboutTxtRef.current, { opacity: 1, duration: 2 });
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  useEffect(() => {
    if (showAboutPlus) {
      gsap.to(aboutPlusRef.current, {
        height: "auto",
        opacity: 1,
        y: 0,
        duration: 0.8,
        ease: "power2.out",
      });
    } else {
      gsap.to(aboutPlusRef.current, {
        height: 0,
        opacity: 0,
        y: -20,
        duration: 0.5,
        ease: "power2.in",
      });
    }
  }, [showAboutPlus]);

  return (
    <div className='about-wrap'>
      <div className="about-basic">
        <img src={`${process.env.PUBLIC_URL}/images/aboutMe.gif`} alt="" />
        <div className="about-txt" ref={aboutTxtRef}>
          <h2>
            !<br/>
            <span> </span>.
          </h2>
          {/* <p>
               CCC  5        . 
            30  '       ,  ?'  , 
            UI/UX       .<br/>
                    . 
               ,        !
          </p> */}
          <p>
                   ,    ,  ,      .         .<br/>
                 .     ,        ! 
          </p>
        </div>
      </div>

      <div className='about-plus' ref={aboutPlusRef} style={{ height: 0, opacity: 0, overflow: "hidden" }}>
        <div className='skills'>
          <h3>Skills<span>.</span></h3>
          <ul>
            <h4> </h4>
            <li>           .</li>
            <li>            .</li>
            <li>       .</li>
          </ul>
          <div className='skills-icon'>
            <img src={`${process.env.PUBLIC_URL}/images/icon/photoshop-icon.png`} alt=''/>
            <img src={`${process.env.PUBLIC_URL}/images/icon/illust-icon.png`} alt=''/>
            <img src={`${process.env.PUBLIC_URL}/images/icon/premiere-icon.png`} alt=''/>
            <img src={`${process.env.PUBLIC_URL}/images/icon/figma-icon.png`} alt=''/>
          </div>
          <ul>
            <h4> </h4>
            <li>      ,     .</li>
            <li>CSS  SCSS   .</li>
            <li>React.js     .</li>
            <li>     ,            .</li>
          </ul>
          <div className='skills-icon'>
            <img src={`${process.env.PUBLIC_URL}/images/icon/html5-icon.png`} alt='HTML5 '/>
            <img src={`${process.env.PUBLIC_URL}/images/icon/css3-icon.png`} alt='CSS3 '/>
            <img src={`${process.env.PUBLIC_URL}/images/icon/javascript-icon.png`} alt='JavaScript '/>
            <img src={`${process.env.PUBLIC_URL}/images/icon/react-icon.png`} alt='React '/>
          </div>
        </div>

        <div className='experience'> 
          <h3>Experience<span>.</span></h3>
          <ul>
            {experienceData.map((list, idx) => (
              <li className='ex-step' key={idx}>
                <p className='ex-date'>{list.date}</p>
                <div className='ex-deco'>
                  <div className='circle'></div>
                  <div className='line'></div>
                </div>
                <p className='ex-txt'>
                  <span>{list.title}</span><br/>
                  <span>{list.dec}</span>
                </p>
              </li>
            ))}
          </ul>
        </div>
      </div>

      <button className='about-more-btn' onClick={clickMoreView}>
        {showAboutPlus ? <span>  <LuChevronUp /></span> : <span> /   <LuChevronDown /></span>}
      </button>
    </div>
  );
};

export default About;
