import React, { useRef } from "react";
import { gsap } from "gsap";
import { useGSAP } from "@gsap/react";

const Intro = () => {
  const introRef = useRef(null);
  const h1Ref = useRef(null);
  const h1MRef = useRef(null);
  const p1Ref = useRef(null);
  const p2Ref = useRef(null);

  useGSAP(() => {
    const tl = gsap.timeline();
    tl.from(h1Ref.current, { opacity: 0, y: -50, duration: 1 }) // h1 
      .from(h1MRef.current, { opacity: 0, y: -50, duration: 1 }, "-=0.5") //  h1  ( )
      .from(p1Ref.current, { opacity: 0, y: -30, duration: 0.8 }, "-=0.3") //   p 
      .from(p2Ref.current, { opacity: 0, y: -30, duration: 0.8 }, "-=0.3"); //   p 
  }, []);

  return (
    <div className="intro-wrap" ref={introRef}>
      <div className="intro-txt">
        <h1 ref={h1Ref}>PORTFOLIO</h1>
        <h1 ref={h1MRef} className="m-title">
          PORT-
          <br />
          FOLIO
        </h1>
        <p ref={p1Ref}>
                  React .
        </p>
        <p ref={p2Ref}>
          100%  , PC, ,           .
        </p>
      </div>
    </div>
  );
};

export default Intro;
